<script>
    import { statuses } from "../stores";
    let statusesShown = false;
    export let status;
    export let workorderId;
    function showStatuses() {
        statusesShown = !statusesShown;
    }

    function updateStatus(newStatus) {
        showStatuses();
        status = newStatus;

        fetch("http://localhost:8080/api/workorders", {
            method: "PUT",
            mode: "cors",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                id: workorderId,
                status,
            }),
        }).then(async (res) => console.log(await res.json()));
    }
</script>

<style>
    .container {
        margin-top: 50px;
        display: flex;
        justify-content: space-between;
    }
    .statuses {
        display: flex;
        flex-direction: row;
        cursor: pointer;
    }
    .active-status {
        display: inline;
        margin-left: 70px;
        font-weight: bold;
        margin-right: 20px;
    }
    .status-container {
        display: flex;
    }
    .status {
        visibility: hidden;
        margin-right: 10px;
        user-select: none;
    }
    .shown {
        visibility: visible;
    }
    /* Colors for statuses*/
    .blue {
        border-bottom: 2px solid #2196f3;
    }
    .red {
        border-bottom: 2px solid #f44336;
    }
    .yellow {
        border-bottom: 2px solid #ffeb3b;
    }
    .green {
        border-bottom: 2px solid #388e3c;
    }
    .blue-hover:hover {
        border-bottom: 2px solid #2196f3;
    }
    .red-hover:hover {
        border-bottom: 2px solid #f44336;
    }
    .yellow-hover:hover {
        border-bottom: 2px solid #ffeb3b;
    }
    .green-hover:hover {
        border-bottom: 2px solid #388e3c;
    }
    /* Colors for statuses */
    .delivery {
        color: #388e3c;
    }
    .location {
        display: flex;
        font-weight: bold;
        margin-top: 10px;
        margin-right: 70px;
    }
    .storage {
        margin-left: 5px;
    }
</style>

<div class="container">
    <div class="statuses">
        <div
            class={'active-status ' + statuses[status].color}
            on:click={showStatuses}>
            {statuses[status].statusText}
        </div>

        <div class="status-container">
            {#each statuses as { statusText, color }, i}
                {#if i != status}
                    <div
                        class={'status ' + color + '-hover' + (statusesShown ? ' shown' : '')}
                        on:click={(_) => {
                            updateStatus(i);
                        }}>
                        {statusText}
                    </div>
                {/if}
            {/each}
        </div>
    </div>
    <div class="location">
        <div class="delivery">Arrived</div>
        <div class="storage">C7</div>
    </div>
</div>
