<script>
    import CollapsedWorkorder from "./Components/CollapsedWorkorder.svelte";
    import ExpandedWorkorder from "./Components/ExpandedWorkorder.svelte";
    import Modal from "./Components/Modal/Modal.svelte";
    import Nav from "./Components/Nav.svelte";
    import { collapsedWorkorders, refreshWorkorders } from "./stores";

    import { workorderExpanded, seeModal } from "./stores";

    refreshWorkorders();
</script>

<style>
    @import url("https://fonts.googleapis.com/css2?family=Montserrat");

    :global(html, body) {
        height: 100%;
    }
    * {
        color: #e3e3e3;
        font-family: "Montserrat", sans-serif;
    }
    main {
        display: flex;
        flex-direction: column;
        height: 100%;
        background-color: #121212;
    }

    .workorders {
        height: 100%;
        margin: 50px;
        margin-top: 10px;
        background-color: #212121;
        border-radius: 10px;
        padding: 40px;
    }
    .titles {
        display: flex;
        justify-content: space-between;
        font-weight: bold;
        font-size: 20px;
        padding: 10px;
        border-bottom: 2px solid white;
    }
</style>

<main>
    <Nav />

    {#if $workorderExpanded === null}
        <div class="workorders">
            <div class="titles">
                <div>Customer</div>
                <div>Device</div>
                <div>Description</div>
                <div>Status</div>
                <div>Location</div>
            </div>
            {#await collapsedWorkorders}
                <p>Loading</p>
            {:then workorders}
                {#each workorders as workorder}
                    <CollapsedWorkorder
                        workorderId={workorder.workorderId}
                        customer={workorder.customer}
                        device={workorder.device}
                        description={workorder.description}
                        status={workorder.status}
                        travelStatus={workorder.travelStatus}
                        location={workorder.location} />
                {/each}
            {/await}
        </div>
    {:else}
        <ExpandedWorkorder />
    {/if}
</main>
{#if $seeModal}
    <Modal />
{/if}
