<script>
    import { workorderExpanded } from "../stores";
    import Note from "./Note.svelte";
    import Statuses from "./Statuses.svelte";
    import { userId } from "../stores";

    let noteInput = "";

    function validateNote() {
        if (noteInput.trim().length) {
            submitNote();
        }
    }

    async function submitNote() {
        return await fetch(`http://localhost:8080/api/notes`, {
            method: "POST",
            mode: "cors",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({
                workorder_id: $workorderExpanded,
                // TODO: Remove
                user_id: $userId,
                contents: noteInput.trim(),
            }),
        })
            .then((_) => {
                workorder = getWorkorder();
                noteInput = "";
            })
            .catch((e) => console.log(e));
    }

    async function getWorkorder() {
        return await fetch(
            `http://localhost:8080/api/workorders?id=${$workorderExpanded}`,
            { mode: "cors" }
        )
            .then(async (response) => await response.json())
            .then((json) => {
                if (json.ok) {
                    return json.message[0];
                } else {
                    throw "Got err response from server: ${json.message}";
                }
            })
            .then((message) => {
                console.log(message);
                return message;
            })
            .catch((e) => console.error(e));
    }

    let workorder = getWorkorder();
</script>

<style>
    .create-note {
        display: flex;
        align-items: center;
    }
    input {
        font-family: "Montserrat", sans-serif;
        height: 50px;
        border-radius: 10px;
        border: none;
        color: #e3e3e3;
        background: transparent;
        flex-grow: 1;
        margin-right: 20px;
    }
    .button {
        font-weight: bold;
        cursor: pointer;
        padding: 10px;
        border-radius: 10px;
        background-color: #388e3c;
    }

    .workorders {
        height: 100%;
        margin: 50px;
        margin-top: 10px;
        background-color: #212121;
        border-radius: 10px;
        padding: 40px;
    }
</style>

{#await workorder}
    <p>loading</p>
{:then workorderContent}
    <Statuses
        status={workorderContent.status}
        workorderId={$workorderExpanded} />
    <div class="workorders">
        <div class="container">
            <div class="notes">
                <div class="create-note">
                    <input
                        bind:value={noteInput}
                        type="text"
                        placeholder="Enter notes here..." />
                    <div class="button" on:click={validateNote}>
                        Create note
                    </div>
                </div>

                {#each workorderContent.notes as { userId, contents, created, next_update }}
                    <Note
                        user={userId}
                        {created}
                        {contents}
                        next_udpate={next_update} />
                {/each}
            </div>
        </div>
    </div>
{/await}
